* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  height: 100vh;
  background-color: #6a5acd;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  width: 400px;
  height: 300px;
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding: 50px;
  display: flex;
  box-shadow: 10px 10px 20px #0000004d, -10px -10px 20px #0000004d;
}

.box {
  text-align: center;
  width: 100%;
}

h2 {
  color: #6a5acd;
  font-size: 24px;
  font-weight: 500;
}

input {
  width: 100%;
  height: 50px;
  border: 3px solid gray;
  border-radius: 5px;
  outline: none;
  margin-top: 8px;
  padding: 0 15px;
  font-size: 18px;
}

input:focus {
  border-color: #6a5acd;
  box-shadow: 3px 3px 6px #0000004d;
}

